<template>
  <div class="beijinTY">
    <fg-header title="战利品"></fg-header>
    <div class="fuzhichenggong">
      <h1 class="chenggongtitle">盗木者</h1>
    </div>
    <div class="guantouqiang">
      <div class="lateral-sliding">
        <p class="nodata" v-if="bombslistall.length==0">暂无盗木者</p>
        <ul class="lateral-sliding-item">
          <draggable
            group="site"
            :move="onMove"
            v-model="bombslistall"
          >
            <transition-group class="lateral-sliding-item">
              <li class="each-img" v-for="item in bombslistall" :key="item.id">
                <img class="GTQimg" :src="item.image" :id="item.id" alt="" />
                <p class="amount">数量：{{item.amount}}</p>
                
              </li>
            </transition-group>
          </draggable>
        </ul>
      </div>
    </div>
    <div class="fuzhichenggong" >
      <h1 class="chenggongtitle">道具</h1>
    </div>
    <div>
      <ul class="marT50 listul">
        <li class="listLi">
          <div class="neibubg">
            <div class="imgdiv">
              <draggable
                @start="onStart"
                v-model="listDG"
                @end="onEnd"
                :move="onMove"
                :options="{
                  group: { name: 'site', pull: 'clone' },
                  sort: true,
                }"
              >
                <img
                  class="listimg imgtop"
                  src="../../assets/images/shengzi.png"
                  alt=""
                />
              </draggable>
            </div>
            <div class="listtext clearfix">
              <p>名称：绳子</p>
              <p v-if="info">数量：{{ info.data.pliers_amount }}个</p>
              <div class="tishi">提示：请托拽绳子到盗木者身上使用，成功后即可获得奖励</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type='text/ecmascript-6'>
import draggable from "vuedraggable";
import {
  user_toolslist_my,
  bombslist_all,
  bombsdisposal,
  bombscost_pliers,
} from "@/api/tools";
export default {
  components: { draggable },

  data() {
    return {
      guantouqingSrc: require("../../assets/images/guantouqiangzhan.png"),
      drag: false,
      toolslist: [],
      bombslistall: [],
      info: "",
      listDG: [
        {
          id: 0,
          image: "../../assets/images/shengzi.png",
        },
      ],
      const:false,
      bombsID:'',
      disabled:false
    };
  },
  created() {},
  mounted() {
    this.info = JSON.parse(localStorage.getItem("userinfo"));
    this.user_toolslist_my();
    this.bombslist_all();
  },
  methods: {

    //move回调方法
    onMove(e, originalEvent) {
      this.bombslistall.forEach((item)=>{
       if (e.relatedContext.element.id == item.id) {
          this.const=true
          this.bombsID=item.id
       }
      })
      
      return false;
      //  console.log(111)
      //  //不允许停靠
      //  if (e.relatedContext.element.id == 1) return false;
      //  //不允许拖拽
      //  if (e.draggedContext.element.id == 4) return false;
      //  return true;
    },
    //开始拖拽事件
    onStart() {
      this.drag = true;
      console.log("开始了");
    },
    //拖拽结束事件
    onEnd(evt) {
      if(this.const){
        this.bombscost_pliers()
      }
    },
    bombscost_pliers() {
      let data = {
        id:this.bombsID,
      };
      bombscost_pliers(data)
        .then((res) => {
          if(res.data.code=='success'){ 
             let dataa = {
            amount: 1,
            id: this.bombsID,
            ...this.$datapublic
          };
          bombsdisposal(dataa)
            .then((resa) => {
               if(resa.data.code=='success'){
                 this.const=false
                  this.bombsID=''
                  this.$toast("捆绑成功");
                  this.bombslist_all();
              }else{
                   this.const=false,
                  this.bombsID=''
                this.$toast(resa.data.msg)
              }
             
              
            })
            .catch((errora) => {
              this.const=false,
              this.bombsID=''
              reject(errora);
            });


          }else{this.$toast(res.data.msg)}
         
        })
        .catch((error) => {
          this.const=false,
          this.bombsID=''
          reject(error);
        });
    },
    user_toolslist_my() {
      user_toolslist_my()
        .then((res) => {
            if(res.data.code=='success'){ 
             this.toolslist = res.data.data;
          }else{this.$toast(res.data.msg)}
         
        })
        .catch((error) => {
          reject(error);
        });
    },
    bombslist_all() {
      bombslist_all()
        .then((res) => {
        if(res.data.code=='success'){
          this.bombslistall = res.data.data;
          this.$store.dispatch('getUserInfo')
              setTimeout(() => {
                this.info = JSON.parse(localStorage.getItem("userinfo"));
              },800);
        }else{this.$toast(res.data.msg)}
          
        })
        .catch((error) => {
          reject(error);
        });
    },
  },
  filters: {},
};
</script>
<style lang="less" scoped>
