  padding-left: 20px;
  width: 240px;
}
.marT50 {
  margin-top: 30px;
}
.imgdiv {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 230px;
  margin: 0 10px;
}
.listimg {
  width: 100%;
  height: auto;
}
.listbtn {
  text-align: center;
  margin-top: 10px;
}

.guantouqiang {
  margin-top: 30px;
  width: 100%;
  height: 435px;
  background: url("../../assets/images/changbeijin.png") no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  margin-bottom: 30px;
}
.lateral-sliding {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  height: 100%;
}
.lateral-sliding-item {
  display: flex;
  margin-right: 5px;
}
.each-img {
  width: 286px;
  height: 370px;
  margin: 34px 20px;
  background-color: #f0c556;
  border-radius: 20px;
  position: relative;
}
.GTQimg {
  width: 337px;
  height: 297px;
  margin-top: 30px;
}
.fuzhichenggong {
  width: 364px;
  height: 107px;
  background: url("../../assets/images/bgkuang.png") no-repeat 100% 100%;
  background-size: 100% 100%;
  margin: auto;
  margin-top: 109px;
  color: #fff;
}
.chenggongtitle {
  line-height: 50px;
  font-weight: bold;
  font-size: 42px;
  text-align: center;
  padding-top: 20px;
}
.daoju {
  height: 576px;
}
.textbox {
  height: 460px;
  margin-top: 50px;
  color: #a53c18;
  font-size: 26px;
}
.DGMC {
  display: flex;
  justify-content: space-between;
  margin: 30px;
  align-items: center;
  justify-items: center;
}
.DGimg {
  height: 330px;
  display: flex;
  align-items: center;
  justify-items: center;
  margin: auto;
}
.GJimg {
  display: block;
  width: auto;
  height: auto !important;
  max-height: 330px;
  margin: auto;
}
.imgtop {
  margin-top: 100px;
}
.amount{
    color: #A53C18;
    font-size:26px;
    font-weight: bold;
    padding-top: 10px;
}
.nodata{
  text-align: center;
  width: 100%;
  padding-top: 180px;
}
</style>
